$font-Brush:'Brush';
$font-Brush2:'Brush2';
@font-face {
    font-family: 'Droid-Bold';
    font-style: normal;
    src: url('../fonts/DroidSerif/DroidSerif-Bold.ttf'); 
}
@font-face {
    font-family: 'Droid-Bold-Italic';
    font-style: normal;
